<template>
  <div style="height:85vh">
    <el-form ref="form" :model="form" label-width="80px">
      <!--      <el-form-item label="">
              <el-input v-model="form.loginPhone" style="width: 30%"></el-input>
            </el-form-item>-->
      <!--      <el-form-item label="">
              <el-input v-model="form.taskName" style="width: 35%"></el-input>
            </el-form-item>-->
      <el-form-item label="">
        <el-select v-model="form.channel" @change="changeChannel">
          <el-option
            v-for="item in channelList"
            :key="item.id"
            :label="item.channelName"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" v-if="form.channel==2">
        <el-checkbox-group v-model="checkedSession">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="" v-if="form.channel==3">
        <el-checkbox-group v-model="checkedSession"
                           :min="0"
                           :max="3"
        >
          <el-checkbox v-for="(session,index) in chnMuSessions" :label="session" :key="session">{{ session }}
          </el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="" v-if="form.channel==1">
        <el-checkbox-group v-model="checkedSession"
                           :min="0"
                           :max="4"
        >
          <el-checkbox v-for="(session,index) in jntMuSessions" :label="session" :key="session">{{ session }}
          </el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="currentUserInfoId">
          <el-option
            v-for="item in currentUserIdList"
            :key="item.id"
            :label="item.userName"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <!--      <el-form-item label="">
              <el-select v-model="form.venue">
                <el-option
                  v-for="item in venueList"
                  :key="item.id"
                  :label="item.venueName"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.session">
                <el-option
                  v-for="item in sessionList"
                  :key="item.id"
                  :label="item.sessionName"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>-->
      <el-form-item label="">
        <el-date-picker type="date" placeholder="" v-model="form.useDate"
                        format="yyyy-MM-dd"></el-date-picker>
      </el-form-item>
      <el-form-item label="" style="height: 55vh">
        <el-button type="primary" @click="addUser" round v-if="!isAddUser"></el-button>
        <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="userText"
          v-if="isAddUser"
          style="width: 50%"
          @input="ok"
        >
        </el-input>
        <!--      <el-button type="primary" @click="ok" v-if="isAddUser" round></el-button>-->
        <div v-if="showUserList">
          <el-table :data="userList" style="overflow:auto;height: 52vh">
            <el-table-column
              lable=""
              type="index"
              width="50">
            </el-table-column>
            <el-table-column
              prop="userName"
              label="">
            </el-table-column>
            <el-table-column
              prop="IDCard"
              label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  round
                  size="mini"
                  type="primary" @click="deleteUser(scope.$index)" v-if="!scope.row.id">
                </el-button>
                <el-button
                  round
                  size="mini"
                  type="primary" @click="operatorUser(scope.row)" v-if="scope.row.id&&scope.row.yn==false">
                </el-button>
                <el-button
                  round
                  size="mini"
                  type="primary" @click="operatorUser(scope.row)" v-if="scope.row.id&&scope.row.yn!=false">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button type="warning" @click="close" round style="margin-left: 31vw"></el-button>
        <el-button type="primary" @click="onSubmit" round></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

import {get, post} from '../../request'

export default {
  name: 'TaskEditView',
  props: {
    taskInfo: {}
  },
  data () {
    return {
      form: {
        'taskName': null,
        'userId': null,
        'channel': null,
        'venue': 1,
        'session': 23,
        'userInfoId': null,
        'source': 0
      },
      currentUserInfoId: null,
      channelList: [
        {
          'id': 0,
          'channelName': ''
        },
        {
          'id': 1,
          'channelName': ''
        },
        {
          'id': 2,
          'channelName': ''
        },
        {
          'id': 3,
          'channelName': ''
        }
      ],
      userIdList: [],
      currentUserIdList: [],
      venueList: [
        {
          'id': 1,
          'venueName': ''
        }
      ],
      sessionList: [
        {
          'id': 23,
          'sessionName': ''
        }
      ],
      isAddUser: false,
      userText: '',
      userList: [],
      showUserList: false,
      session: ['0', '1'],
      checkedSession: [],
      chnMuSessions: ['09:00-11:00', '11:00-13:30', '13:30-16:00'],
      jntMuSessions: ['08:00-09:00', '09:00-10:00', '10:00-11:00', '11:00-12:00']
    }
  },
  /* watch: {
     'userText': 'getNewData',
   },*/
  methods: {
    edit () {
      if (Object.keys(this.taskInfo).length <= 0) {
        return
      }
      if (this.taskInfo.channel == 2) {
        if (this.taskInfo.session) {
          let currentSession = []
          currentSession.push(this.taskInfo.session.toString())
          this.session = currentSession
        }
      }
      if (this.taskInfo.channel == 3) {
        let currentSession = []
        currentSession.push(this.taskInfo.session)
        this.checkedSession = currentSession
      }
      this.form = this.taskInfo
      this.userList = this.taskInfo.userList
      this.showUserList = true
    },
    addUser () {
      this.isAddUser = true
      this.userText = ''
    },
    ok () {
      let regex = /(.+?)\s+(.+)/g
      let list = []
      let match
      while ((match = regex.exec(this.userText)) !== null) {
        let name = match[1]
        let id = match[2]
        let obj = {
          userName: name,
          IDCard: id.toUpperCase()
        }
        list.push(obj)
      }
      this.userList = this.userList.concat(list)
      this.isAddUser = false
      this.showUserList = true
    },
    onSubmit () {
      this.form.session = '23'
      this.form.venue = 1
      if (this.form.channel == 0 && this.userList.length > 15) {
        this.$alert('15', '')
        return
      }
      if (this.form.channel != 0) {
        this.form.session = this.checkedSession.join(',')
      }
      this.form.userInfoId = this.currentUserInfoId
      this.form.userList = this.userList
      post('/ticket/add/taskInfo', this.form).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          this.$notify.success({
            title: '',
            duration: 1000
          })
          this.close()
        }
      })
    },
    deleteUser (index) {
      this.userList.splice(index, 1)
    },
    operatorUser (row) {
      let operatorYn=row.yn
      get('/ticket/operator/detail', {
          id: row.id,
          yn: !operatorYn
        }
      ).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          this.$notify.success({
            title: '',
            duration: 1000
          })
          row.yn=!operatorYn
          console.log(11111)
          return row
        }
      })
    },
    close () {
      this.$emit('close')
    },
    getUserIdList () {
      let queryParam = {
        userName: '',
        account: ''
      }
      post('/ticket/account/list', queryParam).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          this.userIdList = res.data
          this.edit()
          this.changeChannel()
          this.currentUserInfoId = this.form.userInfoId
          this.checkedSession = this.form.session.split(',')
        }
      })
    },
    changeChannel () {
      this.checkedSession = []
      this.currentUserInfoId = null
      let newUserIdList = []
      for (let o of this.userIdList) {
        if (o.channel == this.form.channel) {
          newUserIdList.push(o)
        }
      }
      this.currentUserIdList = newUserIdList
    }
  }
}
</script>

<style scoped>

</style>



