<template>
  <div>
    <el-form :inline="true" :model="queryParam">
      <el-form-item label="">
        <el-select v-model="queryParam.channel" clearable>
          <el-option
            v-for="item in channelList"
            :key="item.id"
            :label="item.channelName"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="queryParam.userInfoId" clearable>
          <el-option
            v-for="item in userIdList"
            :key="item.id"
            :label="item.userName"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="queryParam.useDate"
          type="date"
          placeholder=""
          format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="queryParam.userName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="queryParam.yn" clearable>
          <el-option
            v-for="item in ynList"
            :key="item.id"
            :label="item.label"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="queryParam.done" clearable>
          <el-option
            v-for="item in doneList"
            :key="item.id"
            :label="item.label"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="queryParam.payment" clearable>
          <el-option
            v-for="item in paymentList"
            :key="item.id"
            :label="item.label"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit" size="small" round></el-button>
        <el-button type="primary" @click="addTask" size="small" round></el-button>
        <el-button type="primary" @click="initSearch" size="small" round></el-button>
      </el-form-item>
    </el-form>
    <div>
      <el-table
        :data="taskData"
        ref="multipleTable"
        border
        height="77vh"
        style="width: 100%; margin-top: 10px"
        :span-method="objectSpanMethod"
        @selection-change="handleSelectionChange"
        class="currentTable"
      >
<!--              :row-class-name="tableCellStyle"
  -->
        <el-table-column
          prop="taskId"
          label="Id"
          width="60"
        >
        </el-table-column>
        <el-table-column
          prop="accountName"
          label="">
        </el-table-column>
        <el-table-column
          prop="channel"
          label=""
          :width="80"
        >
          <template slot-scope="{ row }">
            <div>{{ channelObj[row.channel] }}</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="userName"
          label="">
          <template slot-scope="{ row }">
            <div>{{ row.userName }}
              <el-link type="success" @click="ticketInspectionCode(row.orderNumber,row.userName)"
                       v-if="row.childrenTicket&&row.channel==0&&row.orderNumber!=null">
              </el-link>
              <span v-if="row.ext!=null">
                <el-tooltip class="item" effect="dark" :content="row.ext" placement="top-start">
                  <el-icon class="el-icon-warning-outline"></el-icon>
                </el-tooltip>
              </span>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="IDCard"
          label="">
        </el-table-column>
        <el-table-column
          :width="50"
          label="">
          <template slot-scope="scope">
            <div>
              <span v-if="scope.row.taskDetailYn==false" style="color: #7bc749"></span>
              <span v-if="scope.row.taskDetailYn==true" style="color: #f56c6c"></span>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="creator"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="createDate"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="price"
          label=""
          :width="50"
        >
        </el-table-column>
        <el-table-column
          prop="useDate"
          label="">
        </el-table-column>
        <el-table-column
          :width="80"
          label="">
          <template slot-scope="scope">
            <el-link class="el-icon-success" type="success" v-if="scope.row.done" :underline="false"></el-link>
            <el-link icon="el-icon-error" type="danger" v-if="!scope.row.done" :underline="false"></el-link>
          </template>
        </el-table-column>
        <el-table-column
          :width="80"
          label="">
          <template slot-scope="scope">
            <el-link class="el-icon-success" type="success" v-if="scope.row.payment&&scope.row.channel==0"
                     :underline="false"></el-link>
            <el-link icon="el-icon-error" type="danger" v-if="!scope.row.payment&&scope.row.channel==0"
                     :underline="false"></el-link>
            <p v-if="scope.row.channel!=0">--</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="updateDate"
          label="">
          <template slot-scope="scope">
            {{ addDate(scope.row) }}
          </template>
        </el-table-column>
        <el-table-column
          type="index"
          label=""
          width="50">
        </el-table-column>
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column label="" prop="option">
          <template slot-scope="scope">
            <el-link
              type="primary" @click="getTask(scope.row)">
            </el-link>
            <el-link
              type="danger" @click="operatorTask(scope.row.taskId,true)" v-if="scope.row.taskYn==false">
            </el-link>
            <el-link
              type="success" @click="operatorTask(scope.row.taskId,false)" v-if="scope.row.taskYn==true">
            </el-link>
            <el-link type="success" @click="pay" v-if="scope.row.channel==0||scope.row.channel==2"></el-link>
            <el-link
              type="danger" @click="init">
            </el-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        :current-page="page.pageNum"
        :page-size="30"
        :page-sizes="[30,50, 100]"
        :total="page.total"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
      top="2vh"
      :visible.sync="showDialog"
      style="height: 50em;overflow: unset"
      :before-close="closeDialog"
    >
      <taskEditView @close="closeDialog" v-if="showDialog" :taskInfo="taskInfo" ref="taskEditView"></taskEditView>
    </el-dialog>
    <el-dialog
      :visible.sync="showPayDialog"
      style="height: 50em;overflow: unset"
      width="20%"
      :before-close="closePayDialog"
    >
      <div id="qrcodeImg" style="text-align: center" v-if="showPayPic"></div>
    </el-dialog>
    <el-dialog
      :visible.sync="showTicketInspectionImg"
      style="height: 50em;overflow: unset"
      width="20%"
      :before-close="closeTicketInspectionImg"
    >
      <div id="ticketInspectionImg" style="text-align: center" v-if="showImg">
        <p style="font-size: medium; font-weight: bolder;margin-bottom: 5px">{{ this.currentUserName }}</p>
      </div>
    </el-dialog>
    <audio
      ref="audio"
    >
      <source src="../../../static/ding.mp3"/>
    </audio>
  </div>
</template>

<script>
import taskEditView from './TaskEditView'
import QRCode from 'qrcodejs2'
import {get, post} from '../../request'

export default {
  name: 'TaskView',
  components: {
    taskEditView
  },
  created () {
    this.currentUser = Date.now()
    this.initWebSocket()
  },
  mounted () {
    this.onSubmit()
    this.getUserIdList()
  },
  data () {
    return {
      ynList: [
        {
          id: 0,
          label: ''
        }, {
          id: 1,
          label: ''
        }
      ],
      doneList: [
        {
          id: 0,
          label: ''
        }, {
          id: 1,
          label: ''
        }
      ],
      paymentList: [
        {
          id: 0,
          label: ''
        }, {
          id: 1,
          label: ''
        }
      ],
      queryParam: {
        channel: '',
        phone: '',
        useDate: '',
        yn: 0,
        done: null,
        payment: null,
        userName: ''
      },
      taskData: [],
      page: {
        pageNum: 1,
        pageSize: 30,
        total: 0
      },
      showDialog: false,
      msg: '',
      websocketCount: -1,
      //
      queryCondition: {
        type: 'message',
      },
      ticketId: [],
      selectTicket: [],
      payInfo: {},
      payUrl: '',
      showPayDialog: false,
      taskInfo: {},
      number: 0,
      currentUser: '',
      channelObj: {
        '0': '',
        '1': '',
        '2': '',
        '3': '',
      },
      channelList: [
        {
          'id': 0,
          'channelName': ''
        },
        {
          'id': 1,
          'channelName': ''
        },
        {
          'id': 2,
          'channelName': ''
        },
        {
          'id': 3,
          'channelName': ''
        }
      ],
      showPayPic: false,
      userIdList: [],
      showTicketInspectionImg: false,
      showImg: false,
      currentUserName: ''
    }
  },
  watch: {
    showPayDialog: function () {
      setTimeout(() => {
        this.qrcode()
      }, 1000)
      /*      //materielIddata
            this.$nextTick(function () {
              if (this.showPayDialog) {
                //
                this.qrcode(this.payUrl);
              }
            });*/
    }
  },
  methods: {
    initSearch () {
      this.queryParam = {
        channel: '',
        phone: '',
        useDate: '',
        yn: 0,
        done: null,
        payment: null,
        userName: ''
      }
    },
    initWebSocket () {
      let userName = localStorage.getItem('user')
      let ws = `ws://42.51.40.37/ticket/api/pushMessage/${userName}`
      //let ws = `ws://localhost:8082/ticket/api/pushMessage/${userName}`
      this.websock = new WebSocket(ws)
      this.websock.onmessage = this.websocketOnMessage
      this.websock.onopen = this.websocketOnOpen
      this.websock.onerror = this.websocketOnError
      this.websock.onclose = this.websocketClose
    },
    // websocket(send)
    websocketOnOpen () {
      console.log('websock')
    },
    // 
    websocketOnError () {
      this.$notify.error({
        title: 'scoket',
        message: 'scoket',
        duration: 2000
      })
    },
    // 
    websocketOnMessage (e) {
      let res = JSON.parse(e.data)
      this.$notify({
        title: res.title,
        dangerouslyUseHTMLString: true,
        message: res.msg,
        duration: res.time,
        type: 'plain'
      })
      if (res) {
        this.doDing()
      }
      this.onSubmit()
    },
    // 
    websocketSend (Data) {
      this.websock.send(Data)
    },
    doDing () {
      this.$refs.audio.play()
    },
    // 
    websocketClose (e) {
      this.initWebSocket()
      console.log('', e)
    },
    onSubmit () {
      this.queryParam.page = this.page
      post('/ticket/task/list', this.queryParam).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          this.taskData = res.data.list
          this.page.total = res.data.total
          this.page.pageSize = res.data.pageSize
        }
      })
    },
    getUserIdList () {
      let queryParam = {
        userName: '',
        account: ''
      }
      post('/ticket/account/list', queryParam).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          this.userIdList = res.data
        }
      })
    },
    addTask () {
      this.showDialog = true
      setTimeout(() => {
        this.$refs.taskEditView.getUserIdList()
      }, 1000)
    },
    mergeCol (id, rowIndex) {
      // 
      // id
      // rowIndex
      var idName = this.taskData[rowIndex][id] // 
      if (rowIndex > 0) {
        // 
        // eslint-disable-next-line eqeqeq
        if (this.taskData[rowIndex][id] != this.taskData[rowIndex - 1][id]) {
          // 
          var i = rowIndex
          var num = 0 // inum
          while (i < this.taskData.length) {
            // table
            if (this.taskData[i][id] === idName) {
              // 
              i++ // 1
              num++ // num1
            } else {
              i = this.taskData.length // table
            }
          }
          this.number = num
          return {
            rowspan: num, // 
            colspan: 1,
          }
        } else {
          return {
            rowspan: 0, // rowspan0
            colspan: 1,
          }
        }
      } else {
        // 
        let i = rowIndex
        let num = 0
        while (i < this.taskData.length) {
          // table
          if (this.taskData[i][id] === idName) {
            i++
            num++
          } else {
            i = this.taskData.length
          }
        }
        this.number = num
        return {
          rowspan: num,
          colspan: 1,
        }
      }
    },
    objectSpanMethod ({row, column, rowIndex, columnIndex}) {
      // 
      switch (
        columnIndex // 
        ) {
        // 0,1 0,1
        /*case 0:
          return this.mergeCol("account", rowIndex);
        case 1:
          return this.mergeCol("account", rowIndex);*/
        case 15:
          return this.mergeCol('taskId', rowIndex)
      }
    },
    handleSizeChange (val) {
      this.page.pageSize = val
      this.onSubmit()
    },
    handleCurrentChange (val) {
      this.page.pageNum = val
      this.onSubmit()
    },
    getTask (row) {
      get('/ticket/get/detail',
        {
          taskId: row.taskId,
          yn: row.taskYn
        }
      ).then(res => {
        this.taskInfo = res.data
        setTimeout(() => {
          this.$refs.taskEditView.getUserIdList()
          //this.$refs.taskEditView.edit();
        }, 200)
        this.showDialog = true
      })
    },
    operatorTask (taskId, yn) {
      get('/ticket/delete', {
          taskId: taskId,
          yn: yn
        }
      ).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          this.onSubmit()
          this.$notify.success({
            title: '',
            duration: 1000
          })
        }
      })
    },
    tableCellStyle ({row, rowIndex}) {
      if (row.channel == 0 && row.done && !row.payment) {
        return 'success-row'
      }
    },
    getMsg () {
      if (!this.queryParam.loginPhone) {
        this.$alert('')
        return
      }
      get('/ticket/phone/msg',
        {
          phoneNum: this.queryParam.loginPhone
        }
      ).then(res => {
        this.$alert(res.data.data, '', {
          confirmButtonText: '',
        })
      })
    },
    closeDialog () {
      this.showDialog = false
      this.taskInfo = {'userList': []}
      this.onSubmit()
    },
    handleSelectionChange (val) {
      this.selectTicket = val
      let currentTaskId = 0
      for (let item of this.selectTicket) {
        if (currentTaskId == 0) {
          currentTaskId = item.taskId
        }
        /*if (item.done !== true) {
          this.$alert("!")
          return;
        }*/
        if (item.taskId != currentTaskId) {
          this.$alert('!')
          let lastElement = val[val.length - 1]
          this.$refs.multipleTable.toggleRowSelection(lastElement, false)
          val.pop()
          this.selectTicket = val
          break
        }
      }
    },
    qrcode () {  //  URL 
      return new QRCode('qrcodeImg', {
        width: 250,
        height: 250,
        text: this.payUrl,
        colorDark: '#000',
        colorLight: '#fff'
      })
    },
    ticketInspectionCode (ticketNum, userName) {  //  URL 
      this.showTicketInspectionImg = true
      this.showImg = true
      this.currentUserName = userName
      setTimeout(() => {
        return new QRCode('ticketInspectionImg', {
          width: 200,
          height: 200,
          text: ticketNum
        })
      }, 1500)
    },
    pay () {
      this.showPayPic = false
      this.payUrl = ''
      let payParam = {}
      let ticketList = []
      let taskDetailIds = []
      let childrenCount = 0
      for (let item of this.selectTicket) {
        taskDetailIds.push(item.id)
        payParam.taskId = item.taskId
        payParam.authorization = item.authorization
        payParam.date = item.useDate
        payParam.loginPhone = item.account
        payParam.userName = item.userName
        payParam.IDCard = item.IDCard
        payParam.orderId = item.orderId
        if (item.childrenTicket == true) {
          childrenCount += 1
        }
        let ticketItem = {}
        ticketItem.id = item.ticketId
        ticketList.push(ticketItem)
      }
      payParam.taskDetailIds = taskDetailIds
      payParam.ticketInfoList = ticketList
      payParam.childTicketNum = childrenCount
      payParam.ticketNum = this.selectTicket.length
      post('/ticket/pay', payParam).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          if (res.data && res.data != '') {
            this.showPayDialog = true
            this.payUrl = res.data
            this.showPayDialog = true
            this.showPayPic = true
            this.qrcode(this.payUrl)
          } else {
            this.$notify.success({
              title: '',
              message: '',
              duration: 2000
            })
          }
        }
      })
    },
    init () {
      if (this.selectTicket.length <= 0) {
        this.$alert('')
        return
      }
      let intiParam = {}
      let req = []
      let currentTaskId
      for (let item of this.selectTicket) {
        currentTaskId = item.taskId
        let payParam = {}
        payParam.id = item.id
        payParam.ticketId = item.ticketId
        payParam.userName = item.userName
        req.push(payParam)
      }
      intiParam.taskId = currentTaskId
      intiParam.taskDetailEntityList = req
      post('/ticket/init/task', intiParam).then(res => {
        if (res.status != 0) {
          this.$notify.error({
            title: '',
            message: res.msg,
            duration: 2000
          })
        } else {
          this.$notify.success({
            title: '',
            duration: 1000
          })
          this.onSubmit()
        }
      })
    },
    addDate (row) {
      let nowDate = row.updateDate
      if (!nowDate || row.channel == 3) {
        return
      }
      let current = new Date(nowDate)
      let newDate
      if (row.channel == 2) {
        newDate = current.setMinutes(current.getMinutes() + 30)
      }
      if (row.channel == 0) {
        newDate = current.setMinutes(current.getMinutes() + 15)
      }
      let rd = new Date(newDate)
      let y = rd.getFullYear()
      let M = rd.getMonth() + 1
      let d = rd.getDate()
      let H = rd.getHours()
      let m = rd.getMinutes()
      let s = rd.getSeconds()
      if (M < 10) {
        M = '0' + M
      }
      if (d < 10) {
        d = '0' + d
      }
      if (row.channel != 1) {
        return (y + '-' + M + '-' + d + ' ' + H + ':' + m + ':' + s)
      }
      return ''
    },
    closePayDialog () {
      this.showPayPic = false
      this.showPayDialog = false
      this.onSubmit()
    },
    closeTicketInspectionImg () {
      this.showImg = false
      this.showTicketInspectionImg = false
    }
  }
}
</script>

<style>
.el-table .success-row {
  background: #dcf1cf;
}

.el-table .error-row {
  background: coral;
}
</style>



